@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
    background: #FCFBFE;
}

.wrapper {
    width: 100%;
    padding: 50px 50px;
}

a {
    color: #000;
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
    margin-bottom: 0.6em;
}

h1 {
    margin-bottom: 50px;
    font-size: 3em;
}

p {
    opacity: 0.7;
    line-height: 1.5em;
}

.city__grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-column-gap: 50px;
    grid-row-gap: 50px;
}

input {
    width: 100%;
    padding: 10px 15px;
    border-radius: 4px;
    outline: none;
    font-size: 18px;
    border: 1px solid #a7a7a7;
    background: #FCFBFE;
}

.input__search {
    margin-bottom: 50px;
}

.leaflet-container {
    width: 100%;
    height: 80vh;
  }
.hero__outer-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 0px;
}
.city__outer-wrapper {
    background: #fff;
    transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1);
    overflow: hidden;
    border: 1px solid #E4E4E6;
}

.city__outer-wrapper:hover { 
    box-shadow: 0px 20px 20px 0px rgba(0,0,0,0.07);
    cursor: pointer;
}

.city__img-wrapper {
    object-fit: cover;
}

.city__img-wrapper img {
    width: 100%;
}

.city__content-wrapper {
    padding: 20px;
}

.city__bottom-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 25px;
}

.city__stats-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
}

.city__stats-item {
    display: flex;
    align-items: center;
}

.city__icon {
    max-width: 16px;
    margin-right: 5px;
}
.table__outer-wrapper {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    border-bottom: 1px solid #E4E4E6;
    border-top: 1px solid #E4E4E6;
    padding: 20px 0px;
}

.table__column-item {
    width: 15%;
}

.nav__outer-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 30px 50px;
    background: #fff;
    border-bottom: 1px solid #E4E4E6;
}

.nav__brand {
    font-weight: 500;
}

.nav__link {
    margin-left: 45px;
    text-decoration: none;
    font-weight: 500;
}

.active {
    color: #1E69D1;
}
